import zTable from '@/components/z-table/z-table.vue';
export default {
	components: {
		zTable
	},
	data() {
		return {
			drivers: [],
			id: '',
			keyword: '',
			columns: [
				{
					title: '序号',
					key: 'index',
				},{
					title: '姓名',
					key: 'name',
				},
				{
					title: '身份证号码',
					key: 'identityNumber',
					width:300
				},
				{
					title: '性别',
					key: 'gender',
				},	
				{
					title: '报考资料提交日期',
					key: 'applyDate',
				}
			]
		};
	},
	onLoad(option = {}) {
		this.id = JSON.parse(option.drivers).id;
		this.fetch();
	},
	methods: {
		onSearch(event) {
			this.fetch({keyword:event.detail})
		},
		tableSelect(val) {
			console.log(val);
		},
		async fetch(params = {}) {
			const { id, $http } = this;
			const res = await $http.get(`api/info/driver`, { page: 0, size: 99,companyId:id, ...params });
			this.drivers = res.content;
		}
	}
};
